import React from 'react'
import Link from 'gatsby-link'

import College from '../assets/img/college.jpg'
import Logo from '../assets/img/logo.png'

const TheCollege = () => (
  <div>
    <section className="nkc-banner hidden-print"></section>
    <main className="nkc-container clearfix">
      <section className="breadcumb hidden-print">
        <div className="container clearfix">
          <div className="col-lg-full col-md-full col-sm-full">
            <p><Link to="/">Home</Link> &rarr; The College</p>
          </div>
        </div>
      </section>
      <div className="clearfix">
        <div className="col-lg-large col-md-large col-sm-full col-print-large clearfix">
          <div className="nkc-container__content clearfix">
            <h1 className="nkc-page-title" id="the-college">The College</h1>
            <p className="nkc-para-intro">One of the premier institutions of higher education— <strong>North Kamrup College</strong>, Baghmara, established on the 17th August, 1979 is situated under the traditionally enlightened district of Assam, Barpeta. It is 12 Kms north from the head quarter of Bajali sub-division.</p>
            <p>The NH 124A from Pathsala to Bhutan is running through Baghmara. Baghmara where the college is located is a place known for trade and business. Historically, Baghmara Bazar was famous for exchange of various goods between the Assamese and Bhutanese. The landscape surrounding the college covers 33 Bighas of land. People residing in the locality are having diverse ethenic identity and colorful cultural heritage.</p>
            <p className="hidden-sm"><img src={College} style={{ border: '13px solid #fafafa' }} title="North Kamrup College Premise" alt="North Kamrup College Logo" /></p>
            <h2>History of the College</h2>
            <p>The people of ther greater Baghmara felt the need for a higher educational institution and the first step in this regard was to establish a college in Arts stream. The public of the locality with their humble monetary contribution and cordial efforts came forward for this noble cause.</p>
            <p>The college started and grown in a remote rural area like Baghmara with everyone's contribution of the locality against many odds. The founder Principal of this college was Prof. Murari Mohan Dutta, a renowned educationist while veteran Physicist Late Dr. Paban Chandra Mahanta (Dean, Faculty of Science, Gauhati University) was the first President of the Governing Body and Late Dr. Ananta Mohan Das, a Physician and a social worker of repute had the privilege of leading the institution as the Secretary of the public body (Governing Body).</p>
            <p>North Kamrup College vows to attain progress and run ahead in the days to come with the unsparing support of the students, the guardians, the society by and large and the alumni as it is launched and meant for attainment of excellence in highter education. Since its inception, this institution has been playing a significant role in the socio-cultural and literary field of Assam. Today it has emerged as a leading college in Assam by keeping place with changing world.</p>
          </div>
        </div>
        <div className="col-lg-medium col-md-medium col-sm-full col-print-medium">
          <div className="nkc-container__sidebar">
            <div className="responsive-table">
              <table className="table-no-border nkc-feature-table">
                <tbody>
                  <tr>
                    <td colSpan="2">
                      <h2><span className="fa fa-fw fa-bookmark-o"></span> North Kamrup College</h2>
                    </td>
                  </tr>
                  <tr>
                    <td>Logo</td>
                    <td><img src={Logo} width="75" title="North Kamrup College Logo" alt="North Kamrup College Logo" /></td>
                  </tr>
                  <tr>
                    <td>Motto</td>
                    <td><q>Aatmodwipobhava</q></td>
                  </tr>
                  <tr>
                    <td>Established</td>
                    <td>1979</td>
                  </tr>
                  <tr>
                    <td>Type</td>
                    <td>Provincialized</td>
                  </tr>
                  <tr>
                    <td>Stream</td>
                    <td>Arts</td>
                  </tr>
                  <tr>
                    <td>Affiliation</td>
                    <td><a href="http://gauhati.ac.in/" target="_blank" rel="noopener noreferrer" title="Gauhati University">Gauhati University</a></td>
                  </tr>
                  <tr>
                    <td>Recognition</td>
                    <td>U. G. C. under 2(f) &amp; 12(B)</td>
                  </tr>
                  <tr>
                    <td>Accreditation</td>
                    <td>C+ by N. A. A. C.</td>
                  </tr>
                  <tr></tr>
                  <tr></tr>
                  <tr>
                    <td>Principal</td>
                    <td><a href="http://surajitbarman.in/" target="_blank" rel="noopener noreferrer" title="Dr. Surajit Barman">Dr. Surajit Barman</a></td>
                  </tr>
                  <tr>
                    <td>President</td>
                    <td>Dr. Manjura Mohan Kalita (Governing Body)</td>
                  </tr>
                  <tr>
                    <td>Students</td>
                    <td>680</td>
                  </tr>
                  <tr>
                    <td>Staff</td>
                    <td>46 (Teaching &amp; Non Teaching)</td>
                  </tr>
                  <tr>
                    <td>Location</td>
                    <td>Baghmara, Barpeta
                      Assam, India
                    </td>
                  </tr>
                  <tr>
                    <td>Area</td>
                    <td>523560.20 Sq. Ft.</td>
                  </tr>
                  <tr>
                    <td>Nickname</td>
                    <td>NKC, NK College</td>
                  </tr>
                  <tr>
                    <td>Website</td>
                    <td><Link to="/" title="Official Website of North Kamrup College">Official Website</Link></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </main>
    <div className="spacer"></div>
  </div>
)

export default TheCollege



